import { Component, OnInit } from '@angular/core';
import { RequestFormService } from "../request-form.service";
import { FormBuilder, FormGroup } from "@angular/forms";


@Component({
  selector: 'app-voltage-transformer-form',
  templateUrl: './voltage-transformer-form.component.html',
  styleUrls: ['./voltage-transformer-form.component.css']
})
export class VoltageTransformerFormComponent implements OnInit {
  public form: FormGroup;
  public ratedVoltageData = [
    {
      value: 0.66,
      maxValue: 0.72
    },
    {
      value: 3,
      maxValue: 3.6
    },
    {
      value: 6,
      maxValue: 7.2
    },
    {
      value: 10,
      maxValue: 12
    },
    {
      value: 15,
      maxValue: 17.5
    },
    {
      value: 20,
      maxValue: 24
    },
    {
      value: 24,
      maxValue: 26.5
    },
    {
      value: 27,
      maxValue: 30
    },
    {
      value: 35,
      maxValue: 40.5
    },
    {
      value: 110,
      maxValue: 126
    },
    {
      value: 150,
      maxValue: 172
    },
    {
      value: 220,
      maxValue: 252
    },
    {
      value: 330,
      maxValue: 363
    },
    {
      value: 500,
      maxValue: 525
    },
    {
      value: 750,
      maxValue: 787
    }
  ];

  public ratedPowerData = [];

  public accuracyClassData=[0.1, 0.2, 0.5, 1, 3];

  public loadTypeData=['Тип нагрузки I (cos x = 0.5..1)', 'Тип нагрузки II ( cos x = 0.8)'];
  public lineCrossData = [];

  public accountWinding = [0.1, 0.2, 0.5, 1, 3];
  public measureWinding = [0.1, 0.2, 0.5, 1, 3];
  public defenceWinding = ['3P', '6P'];

  constructor(
    protected fb: FormBuilder,
    public requestService: RequestFormService
  ) {
    this.form = fb.group({
      ratedVoltage: [''],
      ratedPower:[''],
      loadType:[''],
      transformerAccuracy:[''],

      firstWinding: [''],
      secondWinding: [''],
      thirdWinding: [''],

      resistanceSum:[''],
      lineCross:[''],
      lineLength:[''],
      maxAccuracy:['']

    });
  }

  ngOnInit() {
  }

  public onLoadTypeChange(value:string){
    if(value === 'Тип нагрузки I (cos x = 0.5..1)'){
      this.ratedPowerData = [1, 2, 5, 10, 15, 20];
    }
    if(value=== 'Тип нагрузки II ( cos x = 0.8)'){
      this.ratedPowerData = [10, 15, 20, 25, 30, 45, 50, 70, 100, 150, 200,
      300, 400, 500, 600, 800, 1000, 12000];
    }
  }

}
