import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { RequestFormService } from "../request-form.service";

@Component({
  selector: 'app-net-params-form',
  templateUrl: './net-params-form.component.html',
  styleUrls: ['./net-params-form.component.css'],
  providers: [RequestFormService]
})
export class NetParamsFormComponent implements OnInit {
  public form: FormGroup;
  get getPotential(){
    debugger;
    return this.form.value.potential;
  };

  public ratedVoltageData = [
    {
      value: 0.66,
      maxValue: 0.72
    },
    {
      value:1, 
      maxValue: 1.1
    },
    {
      value: 3,
      maxValue: 3.6
    },
    {
      value: 6,
      maxValue: 7.2
    },
    {
      value: 10,
      maxValue: 12
    },
    {
      value: 15,
      maxValue: 17.5
    },
    {
      value: 20,
      maxValue: 24
    },
    {
      value: 24,
      maxValue: 26.5
    },
    {
      value: 27,
      maxValue: 30
    },
    {
      value: 35,
      maxValue: 40.5
    },
    {
      value: 110,
      maxValue: 126
    },
    {
      value: 150,
      maxValue: 172
    },
    {
      value: 220,
      maxValue: 252
    },
    {
      value: 330,
      maxValue: 363
    },
    {
      value: 500,
      maxValue: 525
    },
    {
      value: 750,
      maxValue: 787
    }
  ];
  public minAmperage: string = '';
  public maxAmperage: string='';
  constructor(
    protected fb: FormBuilder,
    public requestService: RequestFormService
  ) {
    this.form = fb.group({
      potential: ['', [Validators.required, Validators.min(0)]],
      workPotential: ['', [Validators.required, Validators.min(0)]],
      minPower: [''], 
      maxPower: [''],
      minAmperage: [''],
      maxAmperage: ['']
    });
  }

  public calculateMinAmperage(): void {
    
    if (this.form.valid) {
      this.minAmperage = "" + this.form.value.minPower / (Math.sqrt(3) * this.form.value.potential);
    }
  }

  public calculateMaxAmperage(): void {
    if (this.form.valid) {
      this.maxAmperage = "" + this.form.value.maxPower / (Math.sqrt(3) * this.form.value.potential);
    }
  }

  

  ngOnInit() {
  }
}